<template>
    <div class="tabcontrol">
        <div class="tab-control" :class="{active:index===currentindex}" 
        v-for="(item,index) in titles" :key="index" @click="tabcontrol(index)">
            <span>{{item}}</span>
        </div>
    </div>
    
</template>
<script>
export default {
    name:"TabControl",
    props:{
        titles:{
            tyoe:Array,
            default(){
                return []
            }
        }
    },
    data(){
        return{
            currentindex:0
        }
    },
    methods:{
        tabcontrol(index){
            // 
            this.currentindex = index

            // 子组件传给父组件
            this.$emit("tabclick",this.currentindex)

        }
    }
}
</script>
<style lang="less" scoped>
    .tabcontrol{
        width: 100%;
        font-size: 16px;
        display: flex;
        text-align: center;
        justify-content: space-evenly;
        .tab-control{
            background: #ffffff;
            height: 40px;
            line-height: 40px;
            flex: 1;
        }
        .active{
            color: salmon;
            span{
                padding: 5px;
                border-bottom: 3px solid salmon;
            }
        }
    }
</style>