<template>
  <div id="home" class="home">
    <!-- Search搜索 -->
    <van-search v-model="searchData" disabled  placeholder="zhenhong" input-align="center" />
    <tab-control
      class="tabcontrols aa"
      @tabclick="tabclick"
      ref="tabcontrol1"
      v-show="fixed"
      :titles="['流行','新款','精选']"
    ></tab-control>

    <scroll
      class="content"
      ref="scroll"
      :probetype="3"
      @slide="slide"
      :pullupload="true"
      @pullingup="loadmore"
    >
      <!-- Swiper轮播图 -->
      <van-swipe :autoplay="3000">
        <van-swipe-item v-for="(vaule, index) in banners" :key="index">
          <img class="swipeimg" :src="vaule.image" @load="ImageLoad" />
        </van-swipe-item>
      </van-swipe>

      <!-- Grid宫格 -->
      <van-grid :column-num="4" :border="false" icon-size="56px">
        <van-grid-item
          v-for="(item,index) in recommends"
          :key="index"
          :icon="item.image"
          :text="item.title"
        />
      </van-grid>

      <!--本周流行  图片 -->
      <div class="images">
        <img class="auto-img" src="../assets/img/recommend_bg.jpg" alt />
      </div>

      
      <tab-control
        class="tabcontrols"
        @tabclick="tabclick"
        ref="tabcontrol2"
        :titles="['流行','新款','精选']"
      ></tab-control>

      <!-- 商品 -->
      <good-list :goodlist="showgood"></good-list>
    </scroll>

    <!-- 返回顶部 -->
    <back-top @click.native="backtop" v-show="ishas"></back-top>
  </div>
</template>

<script>
// 流行/新款/  content
import TabControl from "../components/content/TabControl";
import GoodList from "../components/content/goods/GoodList";
import BackTop from "../components/content/backtop/backTop";

// common 滚动
import Scroll from "../components/common/Scroll";

import { debounce } from "../common/utils";

import { gethomedata, getgoodsdata } from "../network/home"; //只有用default才不用{}


//命名空间用法
    import {createNamespacedHelpers} from "vuex"
    const {mapState,mapMutations}  = createNamespacedHelpers('shopcart')

export default {
  name: "Home",
  data() {
    return {
      searchData: "",
      banners: [],
      recommends: [],
      tabactive: 0,
      goods: {
        pop: { page: 0, list: [] },
        new: { page: 0, list: [] },
        sell: { page: 0, list: [] }
      },
      ishas: false,

      //吸顶值
      taboffestop: 0,
      isload: false,

      fixed: false
    };
  },
  computed: {
    ...mapState(["height","current"]),
    //计算属性
    showgood() {
      return this.goods[this.current].list;
    }
  },
  created() {
    // 请求轮播图等数据
    this.gethomedata();

    //请求商品数据
    this.getgoodsdata("pop");
    this.getgoodsdata("new");
    this.getgoodsdata("sell");
    // 
    this.$nextTick(()=>{
      
      // 
      // this.$refs.scroll.scrolltop(0, -this.height);
    })
  },
  deactivated() {
    
    // this.$refs.scroll.refresh();
  },
  mounted() {
    //接收，然后刷新图片
    const refresh = debounce(this.$refs.scroll.refresh, 100);
    this.$bus.$on("imageload", () => {
      // 
      // if(this.$refs.scroll.refresh){
      refresh();
      // }
      // this.$refs.scroll.scroll.refresh()
      // 
    });

    //赋值 获取offerstop
    // 
  },
  components: {
    TabControl,
    GoodList,
    Scroll,
    BackTop
  },
  methods: {
    ...mapMutations(["tab","Height"]),
    //监听轮播图是否加载
    ImageLoad() {
      if (!this.isload) {
        this.offsetTop = this.$refs.tabcontrol2.$el.offsetTop;
        // 
        this.isload = true;
      }
    },

    //事件监听
    tabclick(index) {
      this.tab(index)

      this.$refs.tabcontrol1.currentindex = index;
      this.$refs.tabcontrol2.currentindex = index;
    },
    //跳转链接
    // clicks(url){
    //    window.location.href = url
    // },

    // 点击按钮回到顶部
    backtop() {
      this.$refs.scroll.scrolltop(0, 0);
      // 
    },
    //监听按钮  隐藏和显示
    slide(position) {
      // 
      //监听出现 回顶部按钮
      this.ishas = -position.y > 1000;

      // 
      this.Height(-position.y)


      //监听 tabcontrol
      // 
      this.fixed = -position.y > this.offsetTop;
      // 
    },

    //监听下拉加载
    loadmore() {
      
      this.getgoodsdata(this.current);
    },

    //异步调用数据
    gethomedata() {
      gethomedata()
        .then(res => {
          // 
          // this.result = res;
          this.banners = res.data.data.banner.list;
          this.recommends = res.data.data.recommend.list;
        })
        .catch(err => {
          
        });
    },
    getgoodsdata(type) {
      let page = this.goods[type].page + 1;
      getgoodsdata(type, page)
        .then(res => {
          // 
          //es6 ...解析数组
          this.goods[type].list.push(...res.data.data.list);

          //es5
          // for (let i = 0; i < res.data.list.length; i++) {
          //   this.goods[type].list.push(res.data.list[i]);
          // }
          this.goods[type].page += 1;

          this.$refs.scroll.finishpullup();
        })
        .catch(err => {
          
        });
    }
  }
};
</script>
